import React from "react";

import { Divider, Spinner } from "@blueprintjs/core";
import { FormGroup, Slider } from "@blueprintjs/core";

import { Spectrogram } from "react-spectrogram";
import { SpectrogramData } from "react-spectrogram";
import { SpectrogramDatumX, SpectrogramDatumY, SpectrogramDatumZ }  from "react-spectrogram";

import Help from "./common/Help";
import { HOSTNAME } from "../constants";
import { band } from "../types/band";


const windowLengthHelpContent: JSX.Element = (
    <div>
        <p>A simple moving average is commonly used to smooth out short-term fluctuations in the data.</p>
        Select the length of the window. If you do not want to do the simple moving average, then select 1.
    </div>
);

function doDownscale(data: number[], k: number): number[] {
    if (k === 1) return data;

    let result: Array<number> = Array.from({ length: data.length / k });
    let r_i = 0;
    for (let i = k - 1; i < data.length; i += k) {
        let sum = 0;
        for (let j = i; j >= (i - k + 1); j--) {
            sum += data[j];
        }
        result[r_i++] = sum / k;
    }
    return result;
}

function doSimpleMovingAverage(data: number[], k: number): number[] {
    if (k === 1) return data;

    let result: Array<number> = Array.from(data);
    for (let i = k - 1; i < data.length; i++) {
        let sum = 0;
        let r_i = i - k + 1;
        for (let j = i; j >= r_i; j--) {
            sum += data[j];
        }
        result[r_i] = sum / k;
    }
    return result;
}

// Expensive function
function get_xData(ch: string): SpectrogramDatumX {
    // find corresponding freq
    const b = band.find((element) => element.channel === ch);
    if (b !== undefined) {
        // TM1 - all in kHz
        //const carrierSpacing = 1;
        const half_T_u = 1024;
        const cf = Number(b.freq);      // carrier frequency
        const sf = cf - half_T_u;       // start frequency
        return Array.from({ length: 2048 }, (_, i) => sf + i);
        //return Array.from({ length: 2048 }, (_, i) => sf + carrierSpacing*i);
    }
    // this will never happen but ok ts
    return Array.from({ length: 2048 }, (_, i) => i);
}

const default_xData: SpectrogramDatumX = get_xData("12C");

const Spectrum: React.FC = () => {
    const [data, setData] = React.useState<SpectrogramData | null>(null);
    const xDataRef = React.useRef<SpectrogramDatumX>(default_xData);
    const zDataRef = React.useRef<SpectrogramDatumZ>("");
    const minRef = React.useRef<number | undefined>(undefined);
    const maxRef = React.useRef<number | undefined>(undefined);
    const [windowLength, setWindowLength] = React.useState<{
        changedTo: number;
        releasedTo: number;
    }>({ changedTo: 3, releasedTo: 3 });

    React.useEffect(() => {
        function getChannel() {
            fetch(`${HOSTNAME}welle/channel`)
            .then(async (response: Response) => {
                if (response.ok) {
                    const ch: string = await response.text();
                    xDataRef.current = get_xData(ch);
                }
            });
        }

        getChannel();
    }, []);

    React.useEffect(() => {
        function updateSpectrum() {
            const zData: SpectrogramDatumZ = new Date();
            zDataRef.current = zData;

            fetch(`${HOSTNAME}welle/spectrum`)
            .then((response: Response) => {
                if (response.ok) {
                    (zDataRef.current === zData) && response.arrayBuffer()
                    .then((arrayBuffer: ArrayBuffer) => {
                        return new Float32Array(arrayBuffer);
                    }).then((floatArray: Float32Array) => {
                        return Array.from(floatArray);
                    }).then((yData: Array<number>) => {
                        setData({
                            x: xDataRef.current,
                            y: yData,
                            z: zData
                        });
                    });
                }
            });
        }
        // call fetchData function every n-th time of a frame
        const interval = setInterval(updateSpectrum, 96 * 10);

        updateSpectrum();

        return (() => clearInterval(interval));
    }, []);

    const processedData: SpectrogramDatumY = React.useMemo(() => {
        if (data) {
            const k = 2**windowLength.releasedTo;
            const d = doDownscale(data.y, 2);   // from 2048 data points returns 1024 (which is the width of canvas)
            return doSimpleMovingAverage(d, k); // statistics (smooth out short-term fluctuations)
        }
        return [];
        // Intentionally missing dependency 'downsampleState.releasedTo'
        // this must execute only when data changes!
        // eslint-disable-next-line
    }, [data]);

    let yValues: Array<number> = [];
    if (processedData.length !== 0) {
        if (minRef.current === undefined) minRef.current = processedData[0];
        if (maxRef.current === undefined) maxRef.current = processedData[0];
        let minChanged: boolean = false;
        let maxChanged: boolean = false;
        for (let i = 0; i < processedData.length; i++) {
            if (processedData[i] < minRef.current) {
                minChanged = true;
                minRef.current = processedData[i];
            }
            if (processedData[i] > maxRef.current) {
                maxChanged = true;
                maxRef.current = processedData[i];
            }
        }

        const step: number = 5;
        if (minChanged) {
            minRef.current = Math.floor(minRef.current);
            minRef.current -= step + (minRef.current % step);
        }
        if (maxChanged) {
            maxRef.current = Math.floor(maxRef.current);
            maxRef.current += step - (maxRef.current % step);
        }
        for (let value = minRef.current; value < maxRef.current; value += step) {
            yValues.push(value);
        }
    }

    const xValues = xDataRef.current.filter((item, i) => (i % 128 === 0)).slice(1);

    if (data === null) {
        return (
            <>
            <Spinner />
            <p style={{ textAlign: "center" }}>Attempting to fetch spectrum data...</p>
            </>
        );
    }

    return (
        <>
        <div className="db-medium">
            <FormGroup
                label="SMA window length"
                subLabel={<Help content={windowLengthHelpContent} />}
                inline={true}
                className="db-input-element"
            >
                <Slider
                    min={0}
                    max={6}
                    stepSize={1}
                    labelStepSize={1}
                    labelRenderer={(value) => (`${2**value}`)}
                    onChange={(value) => setWindowLength((p) => ({ ...p, changedTo: value }))}
                    onRelease={(value) => setWindowLength((p) => ({ ...p, releasedTo: value }))}
                    value={windowLength.changedTo}
                    intent="primary"
                    showTrackFill={false}
                    vertical={false}
                />
            </FormGroup>
        </div>
        <Divider />
        <Spectrogram
            data={{
                x: data.x,
                y: processedData,
                z: data.z
            }}
            layout={{
                width: 1024,
                paddingTop: 40,
                paddingRight: 0,
                paddingBottom: 40,
                paddingLeft: 70,
                line: {
                    height: yValues.length * 40
                },
                heatmap: {
                    height: 200,
                    marginTop: 32
                },
                scale: {
                    height: 24,
                    marginTop: 32
                }
            }}
            options={{
                xAxis: {
                    displayAxis: true,
                    displayGrid: true,
                    values: xValues,
                    color: "black"
                },
                yAxis: {
                    displayAxis: true,
                    displayGrid: true,
                    values: yValues,
                    color: "black"
                },
                zAxis: {
                    displayAxis: true,
                    displayBins: 10,
                    color: "black"
                },
                theme: "blue-green-red"
            }}
        />
        </>
    );
};

export default Spectrum;
