import React from "react";

import { Button, NonIdealState, Spinner, Tag } from "@blueprintjs/core";
import { ButtonProps } from "@blueprintjs/core";

import { useFetchState } from "../hooks/useFetchState";
import { HOSTNAME } from "../constants";
import { TheToaster } from "../index";


type TypeStatus = {
    label: string;
    isOn: boolean;
    isOk: boolean;
}

type TypeBtnState = "Loading" | "Start" | "Stop";

/**
 * Defines and evaluates the looks of the status element
 * An element might be OFF but that might be OK.
 */
const StatusElement: React.FC<{ status: TypeStatus }> = ({ status }): JSX.Element => {
    let tag: JSX.Element;
    if (status.isOn)
        tag = <Tag intent={status.isOk ? "success" : "danger"} round={true} fill={false}>ON</Tag>;
    else
        tag = <Tag intent={status.isOk ? "success" : "danger"} round={true} fill={false}>OFF</Tag>;
    return (
        <>
            <span>{status.label}</span>
            {tag}
        </>
    );
}

const Widget1 = React.memo(() => {
    const { isLoading, data, error } = useFetchState<TypeStatus[] | undefined>(
        undefined,
        `${HOSTNAME}flask/info/status`,
        undefined,
        5000
    );
    const [btnState, setBtnState] = React.useState<TypeBtnState>("Loading");

    const lockRef = React.useRef<boolean>(true);
    const fetchLockRef = React.useRef<boolean>(true);

    React.useEffect(() => {
        if (data && fetchLockRef.current) {
            const flag: boolean = data.some(element => element.isOk === false);

            if (flag) {
                lockRef.current && setBtnState("Start");
            }
            else {
                lockRef.current && setBtnState("Stop");
            }
        }
    }, [data]);

    React.useEffect(() => {
        // Clean up the mess on unmount
        return () => {
            // prevent setting new state in all asynch functions
            lockRef.current = false;
        };
    }, []);

    const buttonProps: ButtonProps = React.useMemo(() => {
        /* executes appropriate fetch */
        const handleStartStop = async (url: string, start: boolean) => {
            lockRef.current && setBtnState("Loading");
            lockRef.current && (fetchLockRef.current = false);

            fetch(url)
            .then(async (response: Response) => {
                if (response.ok) {
                    TheToaster.show({
                        icon: "tick",
                        intent: "success",
                        message: (<>
                            Successfully {start ? "started" : "stopped"} the application.
                        </>)
                    });
                }
                else {
                    TheToaster.show({
                        icon: "issue",
                        intent: "danger",
                        timeout: 0,
                        message: (<>
                            [Error: {response.status} {response.statusText}] <br />
                            <hr />
                            Unable to {start ? "start" : "stop"} the application!
                        </>)
                    });
                }
            })
            .catch((error: Error) => {
                TheToaster.show({
                    icon: "issue",
                    intent: "danger",
                    timeout: 0,
                    message: (<>
                        [{error.name}] <br />
                        [{error.message}] <br />
                        <hr />
                        Unable to {start ? "start" : "stop"} the application!
                    </>)
                });
            })
            .finally(() => {
                lockRef.current && (fetchLockRef.current = true);
            });
        };

        switch(btnState) {
            case "Start":
                return {
                    intent: "success",
                    disabled: false,
                    loading: false,
                    text: btnState,
                    onClick: () => handleStartStop(`${HOSTNAME}flask/start`, true)
                };
            case "Stop" :
                return {
                    intent: "danger",
                    disabled: false,
                    loading: false,
                    text: btnState,
                    onClick: () => handleStartStop(`${HOSTNAME}flask/stop`, false)
                };
            case "Loading":
            default:
                return {
                    intent: "none",
                    disabled: true,
                    loading: true,
                    text: btnState
                };
        }
    }, [btnState]);

    if (data === undefined && isLoading) {
        return (
            <>
            <Spinner />
            <p style={{ textAlign: "center" }}>Attempting to fetch the current status.</p>
            </>
        );
    }

    if (error !== undefined) {
        if (error instanceof Error) {
            return (
                <NonIdealState
                    icon="issue"
                    title={`[${error.name}]`}
                    description={<>
                        {error.message} <br />
                        Unable to fetch the current status!
                    </>}
                />
            );
        }
        if (error instanceof Response) {
            return (
                <NonIdealState
                    icon="issue"
                    title={`[Error: ${error.status} ${error.statusText}]`}
                    description={"Unable to fetch the current status!"}
                />
            );
        }
    }

    return (
        <div className="db-widget-container">
            <Button
                {...buttonProps}
                alignText="center"
                fill={true}
                large={true}
                outlined={false}
            />
            <div className="db-status-container">
                {data?.map((item, key) => 
                    <StatusElement key={key} status={item} />
                )}
            </div>
        </div>
    );
});

export default Widget1;
