import React from "react"

import { Button, ButtonProps } from "@blueprintjs/core";
import { Popover2 as Popover, Classes } from "@blueprintjs/popover2";

interface Props {
    content: string | JSX.Element | undefined;
    buttonProps?: ButtonProps;
}

const Help: React.FC<Props> = ({ content, buttonProps }) => {
    return (
        <Popover content={content} popoverClassName={Classes.TOOLTIP2}>
            <Button icon="help" minimal={true} intent="primary" style={{"cursor": "help"}} {...buttonProps} />
        </Popover>
    );
};

export default Help;
