import React from "react";

import { Spinner } from "@blueprintjs/core";
import { Button, MenuItem, FormGroup } from "@blueprintjs/core";
import { Select, ItemPredicate, ItemRenderer } from "@blueprintjs/select";
import { NumericInput, Switch, InputGroup } from "@blueprintjs/core";

import Help from "./common/Help";
import { HOSTNAME } from "../constants";
import { TheToaster } from "../index";
import { band, TBand } from "../types/band";


type TGeneralConfig = {
    band: TBand | undefined;
    interval: number;
    agentIdentifier: number;
    agentLabel: string;
    managerHostname: string;
    managerPort: number;
    trapEnabled: boolean;
};

type TypeEval = {
    band: string | undefined;
    interval: string | undefined;
    agentIdentifier: string | undefined;
    agentLabel: string | undefined;
    managerHostname: string | undefined;
    managerPort: string | undefined;
    trapEnabled: string | undefined;
};

function formatBandText(band: TBand): string {
    return `${band.channel} \u2013 ${band.freq} kHz`
}

const renderBand: ItemRenderer<TBand> = (band, { handleClick }) => {
    return (
        <MenuItem
            label={band.key.toString()}
            key={band.key}
            onClick={handleClick}
            text={formatBandText(band)}
        />
    );
};

const filterBand: ItemPredicate<TBand> = (query, bandItem, _index, exactMatch) => {
    const normalizedChannel = bandItem.channel.toUpperCase() ;
    const normalizedQuery = query.toUpperCase();

    if (exactMatch) {
        return (normalizedChannel === normalizedQuery) || (bandItem.freq === normalizedQuery);
    } else {
        return `${bandItem.key} ${bandItem.freq} ${normalizedChannel}`.indexOf(normalizedQuery) >= 0;
    }
};

const BandSelect = Select.ofType<TBand>();

