import React from "react";

import { HOSTNAME } from "../constants";
import { useFetchState } from "../hooks/useFetchState";


type TDeviceInfo = {
    hardware: string;
    software: string;
    network:  string;
    hostname: string;
};

const defaultData: TDeviceInfo = {
    hardware: "My awesome hardware",
    software: "My awesome software",
    network:  "My awesome network",
    hostname: "My awesome hostname"
};

const DeviceInfo: React.FC = () => {
    const {
        // eslint-disable-next-line
        isLoading, data, error
    } = useFetchState<TDeviceInfo>(
        defaultData,
        `${HOSTNAME}flask/info/device`,
        undefined,
        undefined
    );

    const className = (error !== undefined || isLoading ? "bp3-skeleton" : "");

    return (
        <table className="db-table db-th-left">
            <tbody>
            <tr>
                <th className={className}>Hardware:</th>
                <td className={className}>{data.hardware}</td>
            </tr>
            <tr>
                <th className={className}>Software:</th>
                <td className={className}>{data.software}</td>
            </tr>
            <tr>
                <th className={className}>Network:</th>
                <td className={className}>{data.network }</td>
            </tr>
            <tr>
                <th className={className}>Hostname:</th>
                <td className={className}>{data.hostname}</td>
            </tr>
            </tbody>
        </table>
    )
}

export default DeviceInfo;
