import React from "react";

import { H4, Spinner, Button, Card } from "@blueprintjs/core";
import { RangeSlider, RangeSliderProps, Switch, NumericInput } from "@blueprintjs/core";
import { FormGroup } from "@blueprintjs/core";

import Help from "./common/Help";
import { HOSTNAME } from "../constants";
import { TheToaster } from "../index";


type TypeAlarmElement = {
    enabled: boolean;
    low: number;
    high: number;
    trigger: number;
};

type TypeAlarmConfig = {
    snr: TypeAlarmElement;
    ber: TypeAlarmElement;
    power: TypeAlarmElement;
    fiber: TypeAlarmElement;
    signal: TypeAlarmElement
    sync: TypeAlarmElement;
};

type TypeAlarmConfigAction = {
    type: "snr" | "ber" | "power" | "fiber" | "signal" | "sync" | "all";
    payload: any;
};

type TypeAlarmElementAction = {
    type: "enabled" | "low" | "high" | "trigger";
    payload?: any;
};

const enabledHelpContent: JSX.Element = (
    <div>
        If you want this quality indicator <br />
        to be taken into account in the <br />
        evaluation process, then enable this option.
    </div>
);

const thresholdHelpContent: JSX.Element = (
    <div>
        The alarm is generated when the value is outside of the specified range.
    </div>
);

const triggerHelpContent: JSX.Element = (
    <div>
        Trigger time is the waiting time before the alarm is generated. <br />
        Entered value is assumed to be in seconds.
    </div>
);

interface IAlarmConfigElementProps {
    element: string;
    label: string;
    state: TypeAlarmElement;
    dispatchState: React.Dispatch<TypeAlarmElementAction>;
    sliderProps: RangeSliderProps;
}

const AlarmConfigElement: React.FC<IAlarmConfigElementProps> = ({
    element,
    label,
    state,
    dispatchState,
    sliderProps,
}): JSX.Element => {
    return (
        <Card className="db-card db-primary-card" elevation={1}>
            <H4>{label}</H4>

            <FormGroup
                label="Enabled"
                subLabel={<Help content={enabledHelpContent} />}
                labelFor={`db-input-${element}-enabled`}
                inline={true}
                className="db-input-element db-alarm-config-element"
            >
                <Switch
                    id={`db-input-${element}-enabled`}
                    checked={state.enabled}
                    label={undefined}
                    onChange={() => dispatchState({ type: "enabled" })}
                    large={true}
                />
            </FormGroup>
            {
                sliderProps.min === sliderProps.max ? <></> :
            <FormGroup
                label="Threshold"
                subLabel={<Help content={thresholdHelpContent} />}
                disabled={!state.enabled}
                inline={true}
                className="db-input-element db-alarm-config-element"
            >
                <RangeSlider
                    {...sliderProps}
                    onChange={([low, high]) => {
                        if (low !== state.low) dispatchState({ type: "low", payload: low });
                        if (high !== state.high) dispatchState({ type: "high", payload: high });
                    }}
                    value={[state.low, state.high]}
                    disabled={!state.enabled}
                    vertical={false}
                    intent="primary"
                />
            </FormGroup>
            }
            <FormGroup
                label="Trigger"
                subLabel={<Help content={triggerHelpContent} />}
                labelFor={`db-input-${element}-trigger`}
                disabled={!state.enabled}
                inline={true}
                className="db-input-element db-alarm-config-element"
            >
                <NumericInput
                    id={`db-input-${element}-trigger`}
                    min={0}
                    asyncControl={true}
                    disabled={!state.enabled}
                    large={false}
                    fill={true}
                    leftIcon="stopwatch"
                    onValueChange={(trigger) => dispatchState({ type: "trigger", payload: trigger })}
                    placeholder="60"
                    rightElement={undefined}
                    value={state.trigger}
                />
            </FormGroup>
        </Card>
    );
};

const alarmElementReducer = (state: TypeAlarmElement, action: TypeAlarmElementAction): TypeAlarmElement => {
    if (action.type === "enabled") return { ...state, enabled: !state.enabled };
    return { ...state, [action.type]: action.payload };
};

const alarmConfigReducer = (state: TypeAlarmConfig, action: TypeAlarmConfigAction): TypeAlarmConfig => {
    switch(action.type) {
        case "all":
            return action.payload;
        case "snr":
            return { ...state, snr: alarmElementReducer(state.snr, action.payload) };
        case "ber":
            return { ...state, ber: alarmElementReducer(state.ber, action.payload) };
        case "power":
            return { ...state, power: alarmElementReducer(state.power, action.payload) };
        case "fiber":
            return { ...state, fiber: alarmElementReducer(state.fiber, action.payload) };
        case "signal":
            return { ...state, signal: alarmElementReducer(state.signal, action.payload) };
        case "sync":
            return { ...state, sync: alarmElementReducer(state.sync, action.payload) };
    }
};

const defaultAlarmConfig: TypeAlarmConfig = {
    snr: { enabled: true, low: 5, high: 25, trigger: 60 },
    ber: { enabled: true, low: 0, high: 0.25, trigger: 10 },
    power: { enabled: false, low: 20, high: 50, trigger: 10 },
    fiber: { enabled: true, low: 0, high: 1, trigger: 10 },
    signal: { enabled: true, low: 1, high: 1, trigger: 10 },
    sync: { enabled: true, low: 1, high: 1, trigger: 10 }
};

const AlarmConfig = () => {
    const [isLoading, setIsLoading] = React.useState<boolean>(true);
    const [error, setError] = React.useState<Response | Error | undefined>(undefined);
    const [alarmConfig, dispatchAlarmConfig] = React.useReducer(alarmConfigReducer, defaultAlarmConfig);

    // Nothing to evaluate
    const disableSubmit: boolean = false;

    React.useEffect(() => {
        let lock: boolean = true;
        lock && setIsLoading(true);
        fetch(`${HOSTNAME}flask/config/alarm`)
        .then(async (response: Response) => {
            if (response.ok) {
                lock && dispatchAlarmConfig({ type: "all", payload: await response.json() });
                lock && setError(undefined);
                lock && setIsLoading(false);
            }
            else {
                lock && dispatchAlarmConfig({ type: "all", payload: defaultAlarmConfig });
                lock && setError(response);
                lock && setIsLoading(false);
            }
        })
        .catch((error: Error) => {
            lock && dispatchAlarmConfig({ type: "all", payload: defaultAlarmConfig });
            lock && setError(error);
            lock && setIsLoading(false);
        });
        return () => {
            lock = false;
        };
    }, []);

    React.useEffect(() => {
        if (error instanceof Error) {
            TheToaster.show({
                icon: "issue",
                intent: "warning",
                timeout: 6000,
                message: (<>
                    [{error.name}] <br />
                    [{error.message}] <br />
                    <hr />
                    Unable to fetch the current alarm configuration!
                </>)
            });
        }
        if (error instanceof Response) {
            if (error.status === 404) {
                TheToaster.show({
                    icon: "issue",
                    intent: "primary",
                    timeout: 6000,
                    message: (<>
                        [Error: {error.status} {error.statusText}] <br />
                        <hr />
                        Unable to fetch the current alarm configuration! <br />
                        <strong>File alarmConfig.json does not exist yet.</strong>
                    </>)
                });
            }
            else {
                TheToaster.show({
                    icon: "issue",
                    intent: "warning",
                    timeout: 6000,
                    message: (<>
                        [Error: {error.status} {error.statusText}] <br />
                        <hr />
                        Unable to fetch the current alarm configuration!
                    </>)
                });
            }
        }
    }, [error]);

    const handleSubmit = () => {
        TheToaster.show({
            icon: "info-sign",
            intent: "primary",
            message: "Attempting to save new alarm configuration..."
        });

        // POST
        fetch(`${HOSTNAME}flask/config/alarm`,
            {
                method: "POST",
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(alarmConfig)
            }
        )
        .then(async (response: Response) => {
            if (response.ok) {
                TheToaster.show({
                    icon: "tick",
                    intent: "success",
                    message: "Successfully saved new alarm configuration."
                });
            }
            else {
                TheToaster.show({
                    icon: "issue",
                    intent: "danger",
                    timeout: 0,
                    message: (<>
                        [Error: {response.status} {response.statusText}] <br />
                        <hr />
                        Unable to save new alarm configuration!
                    </>)
                });
            }
        })
        .catch((error: Error) => {
            TheToaster.show({
                icon: "issue",
                intent: "danger",
                timeout: 0,
                message: (<>
                    [{error.name}] <br />
                    [{error.message}] <br />
                    <hr />
                    Unable to save new alarm configuration!
                </>)
            });
        });
    };

    if (isLoading) {
        return (
            <>
            <Spinner />
            <p style={{ textAlign: "center" }}>Attempting to fetch current alarm configuration.</p>
            </>
        );
    }

    return (
        <>
        <div className="db-huge-grid-container">
            <AlarmConfigElement
                element="snr"
                label="Signal Noise Ratio"
                state={alarmConfig.snr}
                dispatchState={(action) => dispatchAlarmConfig({ type: "snr", payload: action })}
                sliderProps={{ min: 0, max: 50, stepSize: 1, labelStepSize: 5 }}
            />
            <AlarmConfigElement
                element="ber"
                label="Bit Error Ratio"
                state={alarmConfig.ber}
                dispatchState={(action) => dispatchAlarmConfig({ type: "ber", payload: action })}
                sliderProps={{ min: 0, max: 0.5, stepSize: 0.01, labelStepSize: 0.1 }}
            />
            <AlarmConfigElement
                element="power"
                label="Power"
                state={alarmConfig.power}
                dispatchState={(action) => dispatchAlarmConfig({ type: "power", payload: action })}
                sliderProps={{ min: 0, max: 80, stepSize: 1, labelStepSize: 20 }}
            />
            <AlarmConfigElement
                element="fiber"
                label="FIB Error Ratio"
                state={alarmConfig.fiber}
                dispatchState={(action) => dispatchAlarmConfig({ type: "fiber", payload: action })}
                sliderProps={{ min: 0, max: 1, stepSize: 0.083, labelStepSize: 0.25, labelPrecision: 2 }}
            />
            <AlarmConfigElement
                element="signal"
                label="Signal Present"
                state={alarmConfig.signal}
                dispatchState={(action) => dispatchAlarmConfig({ type: "signal", payload: action })}
                sliderProps={{ min: 1, max: 1, stepSize: 1, labelStepSize: 1 }}
                // in this case, slider does not make sense
            />
            <AlarmConfigElement
                element="sync"
                label="Synchronization"
                state={alarmConfig.sync}
                dispatchState={(action) => dispatchAlarmConfig({ type: "sync", payload: action })}
                sliderProps={{ min: 1, max: 1, stepSize: 1, labelStepSize: 1 }}
                // in this case, slider does not make sense
            />
        </div>

        <Button
            text="Save"
            icon="caret-right"
            intent="primary"
            disabled={disableSubmit}
            large={true}
            outlined={false}
            onClick={handleSubmit}
        />
        </>
    );
};

export default AlarmConfig;
