import React from "react";

import { Tooltip2, Classes } from "@blueprintjs/popover2";

const NOT_AVAILABLE = "N/A";

const style: React.CSSProperties = {
    fontWeight: 600
};

interface IPairProps {
    pair: {
        key: string;
        value: string | number | boolean | undefined;
        unit?: string;
    };
    tooltipContent?: JSX.Element;
    parentTagStyle?: React.CSSProperties;
    keyTagStyle?: React.CSSProperties;
    valueTagStyle?: React.CSSProperties;
}

const Pair: React.FC<IPairProps> = ({
    pair, tooltipContent, parentTagStyle, keyTagStyle, valueTagStyle
}) => {

    // apply Tooltip?
    let key: JSX.Element = <>{pair.key}</>;
    if (tooltipContent) {
        key = (
            <Tooltip2
                className={Classes.TOOLTIP2_INDICATOR}
                content={tooltipContent}
                position="bottom"
            >{pair.key}</Tooltip2>
        );
    }

    // is unit defined?
    let unit: string = "";
    if (pair.unit) {
        unit = " " + pair.unit;
    }

    switch(typeof pair.value) {
        case "string":
            return (
                <div style={parentTagStyle}>
                    <div className="db-overflow" style={{...style, ...valueTagStyle}}>
                        {pair.value === "" ? NOT_AVAILABLE : pair.value + unit}
                    </div>
                    <div className="bp3-text-muted" style={keyTagStyle}>{key}</div>
                </div>
            );
        case "number":
            return (
                <div style={parentTagStyle}>
                    <div className="db-overflow" style={{...style, ...valueTagStyle}}>
                        {pair.value === 0 ? NOT_AVAILABLE : pair.value.toLocaleString() + unit}
                    </div>
                    <div className="bp3-text-muted" style={keyTagStyle}>{key}</div>
                </div>
            );
        case "boolean":
            return (
                <div style={parentTagStyle}>
                    <div className="dp-pair" style={{...style, ...valueTagStyle}}>
                        {pair.value ? "True" : "False"}
                    </div>
                    <div className="bp3-text-muted" style={keyTagStyle}>{key}</div>
                </div>
            );
        default:
            break;
    }

    // rest...
    return (
        <div style={parentTagStyle}>
            <div className="db-overflow" style={{...style, ...valueTagStyle}}>
                {NOT_AVAILABLE}
            </div>
            <div className="bp3-text-muted" style={keyTagStyle}>{key}</div>
        </div>
    );
};

export default Pair;
