import { useState, useEffect } from "react";


type TypeFetchState<T> = {
    isLoading: boolean;
    data: T;
    error: Response | Error | undefined;
}

export function useFetchState<T>(
    initialData: T,
    url: string,
    options: RequestInit | undefined,
    ms: number | undefined
): TypeFetchState<T> {
    const [state, setState] = useState<TypeFetchState<T>>({
        isLoading: true,
        data: initialData,
        error: undefined
    });

    useEffect(() => {
        let lock: boolean = true;
        function fetchData() {
            setState(s => {
                return {
                    ...s,
                    isLoading: true
                };
            });
            fetch(url, options)
            .then(async (response: Response) => {
                if (response.ok) {
                    lock && setState({
                        isLoading: false,
                        data: await response.json(),
                        error: undefined
                    });
                }
                else {
                    lock && setState({
                        isLoading: false,
                        data: initialData,
                        error: response
                    });
                }
            })
            .catch((error: Error) => {
                lock && setState({
                    isLoading: false,
                    data: initialData,
                    error: error
                });
            });
        }

        let id: any = null;
        if (ms !== undefined) {
            id = setInterval(fetchData, ms);
        }
        else {
            fetchData();
        }

        return () => {
            lock = false;
            clearInterval(id);
        };
    }, [initialData, url, options, ms]);

    return state;
}
