import React from "react";

import { Button, ButtonProps, Dialog, Classes } from "@blueprintjs/core";

import Pair from "./common/Pair";

import { TypeMuxService } from "../types/mux";
import { IAudio } from "../hooks/useAudio";
import { HOSTNAME } from "../constants";


const asctyTooltipContent: JSX.Element = (
    <span style={{fontWeight:600}}>Audio Service Component Type</span>
);

const subchidTooltipContent: JSX.Element = (
    <span style={{fontWeight:600}}>Sub-Channel Identifier</span>
);

const dsctyTooltipContent: JSX.Element = (
    <span style={{fontWeight:600}}>Data Service Component Type</span>
);

const scidTooltipContent: JSX.Element = (
    <span style={{fontWeight:600}}>Service Component Identifier</span>
);

interface IServiceProps extends IAudio {
    service: TypeMuxService;
}

const Service: React.FC<IServiceProps> = ({ service, audio, dispatchAudio }) => {
    const [isOpen, setIsOpen] = React.useState<boolean>(false);
    // isOpen is controlling whether the Dialog component is visible
    // The purpose of Dialog component is to show detailed information about
    // that particular service. Dialog component uses portal, therefore it
    // can be anywhere in the code (it is rendered across the whole page)

    const audioButtonProps: ButtonProps = React.useMemo(() => {
        const url_mp3 = `${HOSTNAME}welle${service.url_mp3}`;

        let sanitizedState = audio.state;
        if (audio.src !== url_mp3) {
            // Show green play button when this particular service is not playing
            sanitizedState = "stop";
        }
        if (service.url_mp3 === null) {
            // it is unplayable service => just disable the button
            sanitizedState = "init";
        }

        switch(sanitizedState) {
            case "start":
                // button is loading
                return {
                    intent: "none",
                    disabled: true,
                    loading: true
                };
            case "playing":
                // button is red with stop icon
                return {
                    icon: "stop",
                    intent: "danger",
                    onClick: () => dispatchAudio({ type: "stop" })
                };
            case "init":
                // button is just disabled (no loading)
                return {
                    icon: "cross",
                    intent: "none",
                    disabled: true
                };
            case "stop":
            default:
                // button is green with play icon
                return {
                    icon: "play",
                    intent: "success",
                    onClick: () => dispatchAudio({ type: "start", payload: url_mp3 })
                };
        }
    }, [audio, dispatchAudio, service.url_mp3]);

    const component = service.components[0];

    return (
        <>
        <tr>
            <td className="db-service-label db-overflow" style={{"verticalAlign":"middle"}}>{service.label.label}</td>
            <td style={{"verticalAlign":"middle","textAlign":"center"}}>
                <Button small={true} {...audioButtonProps} />
            </td>
            <td style={{"verticalAlign":"middle","textAlign":"center"}}>
                <Button icon="info-sign" intent="primary" small={true} onClick={() => setIsOpen(true)} />
            </td>
        </tr>
        <Dialog
            isOpen={isOpen}
            onClose={() => setIsOpen(false)}
            title={
                <img
                    width="150px"
                    src={`${HOSTNAME}welle/slide/${service.sid}`}
                    alt={service.label.label}
                />
            }
            isCloseButtonShown={false}
        >
        <div className={Classes.DIALOG_BODY}>
            <div className="db-small-grid-container">
                <Pair pair={{ key: "Dynamic Label Segment", value: service.dls.label }} parentTagStyle={{ gridColumn: "1 / 4" }} valueTagStyle={{ overflow: "initial", whiteSpace: "initial", textOverflow: "initial" }} />
                <Pair pair={{ key: "Label", value: service.label.label }} />
                <Pair pair={{ key: "Short Label", value: service.label.shortlabel }} />
                <Pair pair={{ key: "Program Type", value: service.ptystring }} />
                <Pair pair={{ key: "Source Coding", value: service.mode.split(",")[0] }} />
                <Pair pair={{ key: "Sample Rate", value: service.samplerate, unit: "Hz" }} />
                <Pair pair={{ key: "Bitrate", value: component.subchannel.bitrate, unit: "kbit/s" }} />
                <Pair pair={{ key: "Language", value: service.languagestring }} />
                <Pair pair={{ key: "Service Identifier", value: service.sid }} />
                <Pair pair={{ key: "ASCTy", value: component.ascty }} tooltipContent={asctyTooltipContent} />
            </div>
        </div>
        <div className={Classes.DIALOG_BODY}>
            <div className="db-small-grid-container">
                <Pair pair={{ key: "SubChId", value: component.subchannel.subchid }} tooltipContent={subchidTooltipContent} />
                <Pair pair={{ key: "Start Address", value: component.subchannel.sad }} />
                <Pair pair={{ key: "Capacity Unit", value: component.subchannel.cu }} />
                <Pair pair={{ key: "Language Code", value: service.language }} />
                <Pair pair={{ key: "Program Type Code", value: service.programType }} />
                <Pair pair={{ key: "Protection", value: component.subchannel.protection }} />
                <Pair pair={{ key: "DSCTy", value: component.dscty }} tooltipContent={dsctyTooltipContent} />
                <Pair pair={{ key: "Label", value: component.label.label }} />
                <Pair pair={{ key: "SCId", value: component.scid }} tooltipContent={scidTooltipContent} />
                <Pair pair={{ key: "Transport Mode", value: component.transportmode }} valueTagStyle={{ textTransform: "capitalize" }} />
            </div>
        </div>
        <div className={Classes.DIALOG_FOOTER}>
            <div className={Classes.DIALOG_FOOTER_ACTIONS}>
                <Button onClick={() => setIsOpen(false)}>Close</Button>
            </div>
        </div>
        </Dialog>
        </>
    );
};

export default Service;
