import React from "react";

import { Card, H3, HTMLTable, Callout } from "@blueprintjs/core";

import Pair from "./components/common/Pair";
import Spectrum from "./components/Spectrum";
import CollapsibleSection from "./components/common/CollapsibleSection";
import { IMuxProps, TypeMuxTii } from "./types/mux";


const defaultTiis: TypeMuxTii[] = [
    {
        pattern: 0,
        comb: 0,
        delay: 0,
        delay_km: 0,
        error: 0
    }
];

interface IDashboardProps extends IMuxProps {}

const Dashboard = React.memo<IDashboardProps>(({ mux }) => {

    let t: string = "";
    if (mux !== undefined && mux.utctime.day !== 0) {
        const { year, month, day, hour, minutes, seconds } = mux.utctime;
        const YY: string = year.toString();
        const MM: string = month.toString().padStart(2, '0');
        const DD: string = day.toString().padStart(2, '0');
        const hh: string = hour.toString().padStart(2, '0');
        const mm: string = minutes.toString().padStart(2, '0');
        const ss: string = seconds.toString().padStart(2, '0');
        t = `${YY}-${MM}-${DD}T${hh}:${mm}:${ss}Z`;
    }

    const tiis = (mux ? mux.tii : defaultTiis);
    const tiisClassName = (mux ? "" : "bp3-skeleton");

    return (
        <>
        <Callout intent="primary">
            Do not forget to configure the application before starting the application!
        </Callout>
        <div className="db-medium-grid-container">
            <Card className="db-card db-primary-card bp3-text-large" elevation={1} >
                <Pair pair={{ key: "Bit Error Ratio", value: mux?.qi.ber }} />
            </Card>
            <Card className="db-card db-primary-card bp3-text-large" elevation={1}>
                <Pair pair={{ key: "Signal Noise Ratio", value: mux?.qi.snr, unit: "dB" }} />
            </Card>
            <Card className="db-card db-primary-card bp3-text-large" elevation={1}>
                <Pair pair={{ key: "FIB Error Ratio", value: mux?.qi.fiber }} />
            </Card>
            <Card className="db-card db-primary-card bp3-text-large" elevation={1}>
                <Pair pair={{ key: "RF Power", value: mux?.qi.power, unit: "dBmV" }} />
            </Card>
            <Card className="db-card db-primary-card bp3-text-large" elevation={1}>
                <Pair pair={{ key: "Signal present", value: mux?.qi.signal }} />
            </Card>
            <Card className="db-card db-primary-card bp3-text-large" elevation={1}>
                <Pair pair={{ key: "Synchronized", value: mux?.qi.sync }} />
            </Card>
        </div>
        <div className="db-large-grid-container">
            <Card className="db-card" elevation={1} id="tii">
                <H3>Transmitter Identification Information</H3>
                <HTMLTable
                    bordered={false}
                    condensed={true}
                    interactive={false}
                    striped={true}
                    width="100%"
                    style={{"marginTop": "10px"}}
                    className={tiisClassName}
                >
                <thead>
                    <tr>
                    <th style={{"verticalAlign":"middle"}}>Pattern</th>
                    <th style={{"verticalAlign":"middle"}}>Comb</th>
                    <th style={{"verticalAlign":"middle"}}>Delay</th>
                    <th style={{"verticalAlign":"middle"}}>Delay [km]</th>
                    <th style={{"verticalAlign":"middle"}}>Error</th>
                    </tr>
                </thead>
                <tbody>
                    {tiis.map((tii) =>
                        <tr key={`${tii.pattern}-${tii.comb}`}>
                        <td style={{"verticalAlign":"middle"}}>{tii.pattern}</td>
                        <td style={{"verticalAlign":"middle"}}>{tii.comb}</td>
                        <td style={{"verticalAlign":"middle"}}>{tii.delay}</td>
                        <td style={{"verticalAlign":"middle"}}>{tii.delay_km.toFixed(3)}</td>
                        <td style={{"verticalAlign":"middle"}}>{tii.error.toFixed(3)}</td>
                        </tr>
                    )}
                </tbody>
                </HTMLTable>
            </Card>
            <Card className="db-card" elevation={1}>
                <H3>Ensemble</H3>
                <div className="db-dashboard-card-container">
                    <Pair pair={{ key: "Label", value: mux?.ensemble.label.label }} />
                    <Pair pair={{ key: "Ensemble Identifier", value: mux?.ensemble.id }} />
                    <Pair pair={{ key: "Extended Country Code", value: mux?.ensemble.ecc }} />
                    <Pair pair={{ key: "UTC Time", value: t }} />
                </div>
            </Card>
            <Card className="db-card" elevation={1}>
                <H3>Receiver</H3>
                <div className="db-dashboard-card-container">
                    <Pair pair={{ key: "Hardware Name", value: mux?.receiver.hardware.name }} />
                    <Pair pair={{ key: "Gain", value: mux?.receiver.hardware.gain }} />
                    <Pair pair={{ key: "Software Name", value: mux?.receiver.software.name }} />
                    <Pair pair={{ key: "Software Version", value: mux?.receiver.software.version }} />
                </div>
            </Card>
        </div>

        <CollapsibleSection label="Spectrum">
            <Spectrum />
        </CollapsibleSection>
        </>
    );
});

export default Dashboard;
