import React from "react";

export type TAudioState = "start" | "stop";

export type TAudio = {
    state: TAudioState | "playing" | "init";
    isPlaying: boolean | undefined;
    src: string | undefined;
};

type TAudioPrivateAction = {
    type: TAudioState | "playing" | "init";
    payload?: string;
};

export type TAudioAction = {
    type: TAudioState;
    payload?: string;
}

export interface IAudioProps {
    onSuccess: (state: TAudioState) => void;
    onError: (state: TAudioState) => void;
}

export interface IAudio {
    audio: TAudio;
    dispatchAudio: React.Dispatch<TAudioAction>;
}

const initAudio: TAudio = {
    state: "init",
    isPlaying: undefined,
    src: undefined
};

const initAudioRef: HTMLAudioElement = new Audio(undefined);

const audioReducer = (state: TAudio, action: TAudioPrivateAction): TAudio => {
    switch(action.type) {
        case "start":
            return {
                state: action.type,
                isPlaying: undefined,
                src: action.payload
            };
        case "playing":
            return {
                state: action.type,
                isPlaying: true,
                src: state.src
            };
        case "stop":
            return {
                state: action.type,
                isPlaying: false,
                src: undefined
            };
        case "init":
        default:
            return initAudio;
    }
};

export function useAudio(
    { onSuccess, onError }: IAudioProps
): [IAudio["audio"], IAudio["dispatchAudio"]] {
    const [audio, dispatchAudio] = React.useReducer(audioReducer, initAudio);
    const audioRef = React.useRef<HTMLAudioElement>(initAudioRef);

    const publicDispatchAudio: React.Dispatch<TAudioAction> = dispatchAudio;

    React.useEffect(() => {
        function check(): boolean {
            return audioRef.current.src === audioRef.current.currentSrc;
        }

        if (audio.state === "start") {
            if (audio.src) {
                audioRef.current.src = audio.src;

                audioRef.current.play().then(() => {
                    check() && dispatchAudio({ type: "playing" });
                    check() && onSuccess("start");
                }).catch(() => {
                    check() && dispatchAudio({ type: "stop" });
                    check() && onError("start");
                });
            }
        }
        if (audio.state === "stop") {
            // clean up funtion of the useEffect does basically the same
            audioRef.current.pause();
            onSuccess("stop");
            // but we want to let user know when the audio stopped the usual way
        }
    }, [audio, onSuccess, onError]);

    React.useEffect(() => {
        return () => {
            // audioRef does not refer to a node rendered by React
            // => we can ignore this warning
            // eslint-disable-next-line
            audioRef.current.pause();
            // stop any audio if there is one
        };
    }, []);

    return [audio, publicDispatchAudio];
}
