import React from "react";

import { HTMLTable, Button, NonIdealState } from "@blueprintjs/core";

import Service from "./Service";

import { IMuxServicesProps } from "../types/mux";
import Help from "./common/Help";
import { IAudio } from "../hooks/useAudio";


const audioHelpContent: JSX.Element = (
    <>
    <p>Press <Button icon="play" intent="success" small={true} /> button to start the audio.</p>
    <p>Press <Button icon="stop" intent="danger" small={true} /> button to stop the audio.</p>
    <p>When you change the service, the currently <br />
       playing audio turns off automatically.</p>
    </>
);

const infoHelpContent: JSX.Element = (
    <>
    <p>Additional information of the corresponding service will be <br />
       displayed by pressing <Button icon="info-sign" intent="primary" small={true} /> button.</p>
    <p>Note that some of the fields might be unavailable (N/A) because <br />
       they are not applicable for that specific type of service.</p>
    </>
);

interface IWidget2Props extends IMuxServicesProps, IAudio {}

const Widget2 = ({
    services,
    audio,
    dispatchAudio
}: IWidget2Props) => {
    // When services are undefined, it means that welle is not running.
    if (services === undefined) {
        return (
            <NonIdealState
                icon="issue"
                title="Services not found"
                description="Welle-cli is not running!"
            />
        );
    }

    // When services is empty array, it means that welle was unable to decode FIC.
    // This can be due to weak signal.
    if (services.length === 0) {
        return (
            <NonIdealState
                icon="issue"
                title="Services not found"
                description="Wait..."
            />
        );
    }

    // Sort services by their Sub-Channel Identifier
    services.sort((a, b) => a.components[0].subchannel.subchid - b.components[0].subchannel.subchid);

    return (
        <HTMLTable
            bordered={false}
            condensed={true}
            interactive={false}
            striped={true}
            width="100%"
        >
        <thead>
            <tr>
                <th style={{"verticalAlign":"middle"}}>Label</th>
                <th style={{"verticalAlign":"middle","textAlign":"center"}}>
                <Help content={audioHelpContent} buttonProps={{ small: true }} />
                </th>
                <th style={{"verticalAlign":"middle","textAlign":"center"}}>
                <Help content={infoHelpContent} buttonProps={{ small: true }} />
                </th>
            </tr>
        </thead>
        <tbody>
            {services.map((service) => 
                <Service
                    key={service.sid}
                    service={service}
                    audio={audio}
                    dispatchAudio={dispatchAudio}
                />
            )}
        </tbody>
        </HTMLTable>
    );
};

export default Widget2;
