import React, { useState } from "react";

import { Card, H3, Collapse, Button, Divider } from "@blueprintjs/core";

interface ICollapsibleSectionProps {
    label: string;
    defaultIsOpen?: boolean;
}

const CollapsibleSection: React.FC<ICollapsibleSectionProps> = ({ children, label, defaultIsOpen = false }) => {
    const [isOpen, setIsOpen] = useState<boolean>(defaultIsOpen);

    return (
        <Card className="db-card" elevation={1}>
            <Button
                icon={isOpen ? "caret-right" : "caret-down"}
                minimal={true}
                fill={true}
                large={true}
                outlined={false}
                alignText="left"
                text={ <H3 style={{ margin: 0 }}>{label}</H3> }
                onClick={() => setIsOpen(wasOpen => !wasOpen)}
            />
            <Collapse isOpen={isOpen}>
                <Divider style={{ marginBottom: "1em"}} />
                {children}
            </Collapse>
        </Card>
    );
};

export default CollapsibleSection;
