import React, { useState } from "react";

import { Button, Navbar } from "@blueprintjs/core";
import "normalize.css/normalize.css"
import "@blueprintjs/icons/lib/css/blueprint-icons.css"
import "@blueprintjs/core/lib/css/blueprint.css"
import "@blueprintjs/popover2/lib/css/blueprint-popover2.css";
import "@blueprintjs/select/lib/css/blueprint-select.css";
import "./styles/Dabing.css";

import Dashboard from "./Dashboard";
import Configuration from "./Configuration";
import Documentation from "./Documentation";

import Logo from "./components/common/Logo";
import CollapsibleSection from "./components/common/CollapsibleSection";
import Widget1 from "./components/Widget1";
import Widget2 from "./components/Widget2";

import { TAudioState, useAudio } from "./hooks/useAudio";
import { useFetchState } from "./hooks/useFetchState";
import { TheToaster } from "./index";

import { TypeMux } from "./types/mux";
import { HOSTNAME } from "./constants";


type TPages = "dashboard" | "configuration" | "documentation";

const audioOnSuccess = (state: TAudioState) => {
    TheToaster.show({
        icon: "tick",
        intent: "success",
        message: `Successfully ${state === "start" ? "started" : "stopped"} the audio!`
    });
};

const audioOnError = (state: TAudioState) => {
    TheToaster.show({
        icon: "issue",
        intent: "danger",
        message: `Unable to ${state === "start" ? "start" : "stop"} the audio!`
    });
};

const Dabing: React.FC = () => {
    const [activePage, setPage] = useState<TPages>("dashboard");
    const [audio, dispatchAudio] = useAudio({
        onSuccess: audioOnSuccess,
        onError: audioOnError
    });
    const {
        // eslint-disable-next-line
        isLoading, data, error
    } = useFetchState<TypeMux | undefined>(
        undefined,
        `${HOSTNAME}welle/mux.json`,
        undefined,
        5000
    );

    function renderPage() {
        switch(activePage) {
            case "configuration":
                return <Configuration />;
            case "documentation":
                return <Documentation />;
            case "dashboard":
            default:
                return <Dashboard mux={data} />
        }
    }

    return (
        <>
            <Navbar style={{"padding": "0"}}>
            <div className="db-navbar-grid-container">
                <div className="db-navbar-grid-item">
                    <Navbar.Group align="left">
                        <Navbar.Heading><Logo onClick={() => setPage("dashboard")}/></Navbar.Heading>
                        <Navbar.Divider />
                        <Button
                            minimal={true}
                            icon="dashboard"
                            text="Dashboard"
                            intent={activePage === "dashboard" ? "primary" : "none"}
                            onClick={() => setPage("dashboard")}
                        />
                        <Button
                            minimal={true}
                            icon="cog"
                            text="Configuration"
                            intent={activePage === "configuration" ? "primary" : "none"}
                            onClick={() => setPage("configuration")}
                        />
                        <Button
                            minimal={true}
                            icon="book"
                            text="Documentation"
                            intent={activePage === "documentation" ? "primary" : "none"}
                            onClick={() => setPage("documentation")}
                        />
                    </Navbar.Group>
                </div>
            </div>
            </Navbar>
            <div className="db-dabing-grid-container">
                <main className="db-dabing-grid-item">
                    {renderPage()}
                </main>
                <aside className="db-dabing-grid-item db-aside-panel">
                    <CollapsibleSection label="Status">
                        <Widget1 />
                    </CollapsibleSection>
                    <CollapsibleSection label="Services">
                        <Widget2 services={data?.services} audio={audio} dispatchAudio={dispatchAudio} />
                    </CollapsibleSection>
                </aside>
            </div>
        </>
    );
};

export default Dabing;
